a{
text-decoration: none;
}

.project-list{
    margin: 2.5%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    .project-list__block{
        text-align: center;
        margin: 20px;
        display: grid;
        .block__img{
            img{
               width: 85%;
               border-top: 1rem solid #DBECFF;
               border-left: 2rem solid #DBECFF;
               border-bottom: 1rem solid #DBECFF;
               border-right: 2rem solid #DBECFF;  
            }
        }
        .block__text{
            margin-left: 2.5%;
            padding-top: 25px;
            text-align: left;
            h1{
                padding: 10px 0px;
               font-size: 32px;
               font-weight: 700;
               color: #101214;
            }
            p{
                margin: 5px 0px;
                font-size: 16px;
                font-weight: 400;
                color: #73808D;
            }
        }
    }
}