// Here we write your SCSS style
body{
	margin: 0;
}
.font{
	font-family: 'Inter', sans-serif;
}
.container{
	margin: 10%;
}

section{
	margin-bottom: 10%;
}

.intro{
	.button__back{
		a{
			text-decoration: none;
			font-size: 14px;
			font-weight: 400;
			text-transform: uppercase;
			color: #73808D;
			line-height: 20px;
			letter-spacing: 6px;
		}
	}
	.project__description{
		h1{
			font-size: 64px;
			font-weight: bold;
			line-height: 64px;
			letter-spacing: 1px;
		}
		p{
			font-size: 32px;
			font-weight: 400;
			line-height: 44px;
			color: #73808D;
		}
	}
}

.before-after{
	.container{
		margin: 0 10%;
		h1{
			font-size: 40px;
			font-weight: 700;
			line-height: 52px;
		}
	}
	.image-list{
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		padding: 10% 0;
		background: linear-gradient(to right, #DBECFF, #b6d8ff);
		justify-items: center;
		img{
			width: 90%;
		}
	}
}

.screens{
	.container{
		margin: 0 10%;
		h1{
			font-size: 40px;
			font-weight: 700;
			line-height: 52px;
		}
	}
	.image-list{

		display: grid;
		grid-template-rows: 1fr;
		grid-row-gap: 80px;
		padding: 10% 0;
		justify-items: center;
		background: linear-gradient(to right, #DBECFF, #b6d8ff);
		img{
			width: 90%;
		}
	}
}

.results{
	.container{
		margin: 0 10%;
		h1{
			font-size: 40px;
			font-weight: 700;
			line-height: 52px;
		}
	}
	ul{
		padding: 0;
		list-style-type: none;
		li{
			font-size: 28px;
			font-weight: 400;
			line-height: 42px;
			color: #73808D;
			img{
				width: 28px;
			}
		}
	}
}

footer{
	display: grid;
	grid-template-columns: 1fr 1fr;
	background: #F4F6FC;
	a{
		text-decoration: none;
		padding: 10% 30%;
		text-align: center;
		color: #000000;
		div{
			font-size: 28px;
			font-weight: 700;
			line-height: 42px;
		}
	}
}